import * as THREE from 'three'

let group;
let renderer;
let camera;
let topglow;
let scene;
let global;
let el;
let style;
let canRotation = true;
let timer;

export default (config) => {
  style = config.style;
  el = config.el;
  group = config.group;
  renderer = config.renderer;
  camera = config.camera;
  topglow = config.topglow;
  scene = config.scene;
  global = config.global;

  el.addEventListener('wheel', handleWheel, false)
  el.addEventListener('mousedown', handleMouseDown, false);
  animate();
}

const speed = 5;

let rotateEnd = new THREE.Vector2();
let rotateStart = new THREE.Vector2();


function handleMouseDown(event) {
  event.preventDefault();
  event.stopPropagation();

  clearTimeout(timer)

  rotateStart.set(event.clientX, event.clientY);

  document.addEventListener('mousemove', handleMouseMove, false);
  document.addEventListener('mouseup', handleMouseUp, false);
}


const PI2 = Math.PI / 2;

function handleMouseMove(event) {
  canRotation = false;

  rotateEnd.set(event.clientX, event.clientY);

  let moveY = group.rotation.x + (rotateEnd.y - rotateStart.y) * 0.01;
  let moveX = (rotateEnd.x - rotateStart.x) * 0.01;

  moveY = Math.max(-PI2, Math.min(PI2, moveY));

  group.rotation.x = moveY
  group.rotation.y += moveX

  rotateStart.copy(rotateEnd);
}

function handleMouseUp() {
  timer = setTimeout(function() {
    canRotation = true;
  }, 1000)
  document.removeEventListener('mousemove', handleMouseMove)
  document.removeEventListener('mouseup', handleMouseUp)
}

function handleWheel(event) {

  if (event.deltaY < 0) {
    camera.position.z += speed;
    console.log(camera.position.z)
    topglow.position.z += 2.3;
    // topglow.position.y -= 0.25;
  } else if (event.deltaY > 0) {
    camera.position.z -= speed;
    topglow.position.z -= 2.3;
    // topglow.position.y += 0.25;
  }

  event.preventDefault();
  event.stopPropagation();
}


function animate() {
  // topglow.position.copy(camera.position);
  topglow.rotation.copy(camera.rotation);

  // topglow.position.copy(camera.position);
  // topglow.rotation.y = camera.rotation.y;
  // topglow.rotation.x = camera.rotation.x;
  // topglow.rotation.z = camera.position.z;
  topglow.updateMatrix();
  // topglow.translateZ(-10);

  requestAnimationFrame(animate);
  // camera.rotation.y -= 0.005;
  if (canRotation) {
    group.rotation.y -= 0.005;
  }

  renderer.render(scene, camera);
}
