import * as THREE from 'three'

const colors = [0xdd514c, 0xf37b1d, 0x5867c3, 0x3792ea]

export default function Tube({ opacity, points, lv }) {

  this.length = points.lenght;
  this.points = points;
  this.opacity = opacity || 0.2;
  this.opa = 1;

  this.geometry = new THREE.TubeBufferGeometry(new THREE.CatmullRomCurve3(points), 20, 2, 16, false);

  this.count = this.geometry.attributes.position.count;
  this.pos = this.count;

  this.material = new THREE.ShaderMaterial({
    vertexColors: THREE.VertexColors,
    transparent: true,

    uniforms: {
      opacity: { type: 'f', value: this.opacity },
      lColor: { type: 'c', value: new THREE.Color(colors[lv]) },
      dColor: { type: 'c', value: new THREE.Color(0xffffff) },
    },

    vertexShader: `
      #include <common>
      #include <uv_pars_vertex>
      #include <uv2_pars_vertex>
      #include <envmap_pars_vertex>
      #include <color_pars_vertex>
      #include <fog_pars_vertex>
      #include <morphtarget_pars_vertex>
      #include <skinning_pars_vertex>
      #include <logdepthbuf_pars_vertex>
      #include <clipping_planes_pars_vertex>

      uniform vec3 dColor;
      uniform vec3 lColor;
      uniform float opacity;

      attribute float opa;

      varying vec4 varColor;

      void main() {
        if(opa <= opacity) {
          varColor = vec4(dColor, opa);
        } else {
          varColor = vec4(lColor, opa);
        }

      	#include <uv_vertex>
      	#include <uv2_vertex>
      	#include <color_vertex>
      	#include <skinbase_vertex>
      	#ifdef USE_ENVMAP
      	#include <beginnormal_vertex>
      	#include <morphnormal_vertex>
      	#include <skinnormal_vertex>
      	#include <defaultnormal_vertex>
      	#endif
      	#include <begin_vertex>
      	#include <morphtarget_vertex>
      	#include <skinning_vertex>
      	#include <project_vertex>
      	#include <logdepthbuf_vertex>
      	#include <worldpos_vertex>
      	#include <clipping_planes_vertex>
      	#include <envmap_vertex>
      	#include <fog_vertex>
      }
    `,

    fragmentShader: `
      varying vec4 varColor;

      void main() {
        gl_FragColor = varColor;
      }
    `
  });

  this.start();

  return new THREE.Mesh(this.geometry, this.material);
}

Tube.prototype.start = function() {
  let length = this.count;

  const opas = Array(length).fill(this.opacity);

  const maxPos = length - 3;

  let pos = this.pos;
  for (let i = length; i > 1; i -= 6) {
    // let c = i / length;
    let c = Math.max(0.4, i / length);

    if (pos <= maxPos) {
      opas[pos] = c;
      opas[pos + 1] = c;
      opas[pos + 2] = c;
      opas[pos + 3] = c;
      opas[pos + 4] = c;
      opas[pos + 5] = c;
    }

    pos -= 6;
    if (pos <= 0) {
      break;
    }
  }

  this.pos += 6;
  if (this.pos > 2 * length) {
    this.pos = 0;
  }

  // console.log(opas)
  this.geometry.addAttribute('opa', new THREE.BufferAttribute(new Float32Array(opas), 1));
  // console.log(this.geometry.attributes.opa)
  // this.geometry.addAttribute('opa', new THREE.BufferAttribute(this.opa));
  // this.geometry.attributes.opa = this.opa;

  requestAnimationFrame(() => {
    this.start()
  })
}
