<script>
import * as THREE from 'three';
import control from './control'
import createGlobal from './global'
import createMarker from './marker'
import createLines from './lines'
import createTop from './top'

export default {
  name: 'Global',
  props: ['attacks'],
  mounted: function() {
    const el = this.$el;
    const style = el.getBoundingClientRect();

    const scene = new THREE.Scene();
    const group = new THREE.Mesh(new THREE.Geometry());

    const global = createGlobal();
    const topglow = createTop();

    const camera = new THREE.PerspectiveCamera( 45, style.width / style.height, 0.1, 1500 );
    camera.position.set( 0, -10, 650 );

    camera.add(new THREE.AmbientLight( 0x3355443 ));
    camera.add(new THREE.SpotLight( 0xffffff ));

    scene.add(camera)
    scene.add(topglow);
    scene.add(group);
    // camera.remove(topglow)

    group.add(global);

    const renderer = new THREE.WebGLRenderer({ alpha: true });

    renderer.setSize( style.width, style.height);
    renderer.setPixelRatio(window.devicePixelRatio);

    el.appendChild( renderer.domElement );

    renderer.render(scene, camera);

    this.$watch('attacks', ()=> {
      createMarker(this.attacks, group)
      addLines(this.attacks, group )
    })


    createMarker(this.attacks, group)
    createLines(this.attacks, group)

    control({
      style,
      group,
      global,
      renderer,
      camera,
      topglow,
      scene,
      el
    })
  }
}
