import * as THREE from 'three';


import globalImage from './global.jpg'
import topglow from './top.png'

const radius = 220;

export default () => {
  const texture = new THREE.TextureLoader().load(globalImage);

  const geometry = new THREE.SphereGeometry(220, 220, 220);
  const material = new THREE.MeshPhongMaterial({
    map: texture,
    overdraw: 0.5,
    transparent: true,
  });

  const mesh = new THREE.Mesh(geometry, material);
  return mesh;
}
